
export default {
  props: {
    title: {
      type: String,
      required: true
    },
    unit: {
      type: [String, Array],
      default: ''
    },
    isSelfReport: {
      type: Boolean,
      default: false
    },
    tips: {
      type: String,
      default: ''
    },
    c3Config: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
    unitStr () {
      if (Array.isArray(this.unit)) {
        return this.unit.join(' · ')
      }
      return this.unit
    }
  }
}
